.header-nav .btn-menu {
  -webkit-transition: all .5s ease-out;
  transition: all .5s ease-out;
  cursor: pointer;
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 5; }
  .header-nav .btn-menu .bar {
    width: 2rem;
    height: .2rem;
    margin: .3rem 0 0 0;
    background: white;
    border-radius: 100px;
    -webkit-transition: background 2.5s;
    transition: background 2.5s; }
    .header-nav .btn-menu .bar.dark {
      background: black !important; }
  .header-nav .btn-menu.close {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); }
    .header-nav .btn-menu.close .bar:nth-child(1) {
      -webkit-transform: rotate(45deg) translate(5px, 5px);
              transform: rotate(45deg) translate(5px, 5px); }
    .header-nav .btn-menu.close .bar:nth-child(2) {
      opacity: 0; }
    .header-nav .btn-menu.close .bar:nth-child(3) {
      -webkit-transform: rotate(-45deg) translate(7px, -6px);
              transform: rotate(-45deg) translate(7px, -6px); }
    .header-nav .btn-menu.close .bar.dark {
      -webkit-transition: all .5s ease-out;
      transition: all .5s ease-out;
      background: white !important; }

.header-nav .menu-overlay {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  top: 0;
  width: 100%;
  visibility: hidden; }
  .header-nav .menu-overlay.show {
    visibility: visible;
    opacity: .9; }
  .header-nav .menu-overlay .branding, .header-nav .menu-overlay .nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-flow: column wrap;
            flex-flow: column wrap;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 50%;
    height: 100vh; }
  .header-nav .menu-overlay .branding {
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
    background: #282c34; }
    .header-nav .menu-overlay .branding.show {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0); }
    .header-nav .menu-overlay .branding .portrait {
      border: 2px solid #15b0f6;
      opacity: 1;
      height: 16rem;
      width: 16rem;
      border-radius: 50%;
      -webkit-align-self: center;
              align-self: center; }
  .header-nav .menu-overlay .nav-menu {
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
    list-style-type: none;
    background: #1d2026; }
    .header-nav .menu-overlay .nav-menu.show {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0); }
    .header-nav .menu-overlay .nav-menu li {
      -webkit-transition: all .5s ease-out;
      transition: all .5s ease-out;
      -webkit-transform: translate3d(600px, 0, 0);
              transform: translate3d(600px, 0, 0);
      font-size: 2rem;
      font-weight: 300;
      padding: .5rem 0; }
      .header-nav .menu-overlay .nav-menu li:hover {
        color: #15b0f6; }
      .header-nav .menu-overlay .nav-menu li.show {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0); }
      .header-nav .menu-overlay .nav-menu li .active-link {
        color: #15b0f6;
        border-bottom: 2px solid #15b0f6; }
  .header-nav .menu-overlay .nav-menu li:nth-child(1) {
    -webkit-transition-delay: 0.1s;
            transition-delay: 0.1s; }
  .header-nav .menu-overlay .nav-menu li:nth-child(2) {
    -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s; }
  .header-nav .menu-overlay .nav-menu li:nth-child(3) {
    -webkit-transition-delay: 0.3s;
            transition-delay: 0.3s; }
  .header-nav .menu-overlay .nav-menu li:nth-child(4) {
    -webkit-transition-delay: 0.4s;
            transition-delay: 0.4s; }

