/* ===== MOBILE STYLES ===== */
@media screen and (max-width: 500px) {
  #home-main {
    padding-top: 20vh !important; }
    #home-main .main-header {
      line-height: 1 !important;
      padding-bottom: 1rem; } }

@media screen and (max-width: 768px) {
  .menu-overlay {
    -webkit-flex-flow: column wrap;
            flex-flow: column wrap; }
    .menu-overlay .branding, .menu-overlay .nav-menu {
      width: 100% !important; }
      .menu-overlay .branding.show, .menu-overlay .nav-menu.show {
        -webkit-transform: translate3d(0, 0, 0) !important;
                transform: translate3d(0, 0, 0) !important; }
    .menu-overlay .branding {
      max-height: 35vh !important;
      -webkit-transform: translate3d(-100%, 0, 0) !important;
              transform: translate3d(-100%, 0, 0) !important;
      padding: 1rem; }
      .menu-overlay .branding .portrait {
        width: auto !important;
        height: 100% !important; }
    .menu-overlay .nav-menu {
      height: 65vh !important;
      -webkit-transform: translate3d(100%, 0, 0) !important;
              transform: translate3d(100%, 0, 0) !important; } }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  position: relative;
  width: 100vw;
  height: 100vh;
  background: url(/Sass-Portfolio/static/media/bg-light-2.9aa74de5.jpeg);
  background-color: #282c34;
  color: white;
  background-attachment: fixed;
  background-size: cover;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
  height: 100%; }
  body::after {
    content: '';
    position: absolute;
    top: 0;
    z-index: 1;
    width: 100vw;
    height: 100vh; }
  body::after {
    z-index: -1 !important;
    background: rgba(16, 17, 20, 0.9); }

